.dropdownButton {
	background-color: #040409;
    border: none;
    color: white;
    width: 200px;
    height: 80px;
}

.dropdownButtonItem {
	background-color: #040409;
    border: none;
    color: white;
    width: 150px;
    height: 45px;
    opacity: 0.8;
    transition: opacity .9s;
}

.dropdownButtonItem:hover {
	opacity: 0.98;
	border-style: inset;
	border-width: 1px;
	border-color: white;
}


#onTop {
  position: absolute;
}


.ReactModal__Overlay {
    z-index: 99;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255,255,255,0.5);
}

.ReactModal__Content {
    position: absolute;
    left: 2.5rem;
    right: 2.5rem;
    top: 2.5rem;
    bottom: 2.5rem;
    height: 600px;
    border: 1px solid #ccc;
    background-color: #fff;
    transform: translate(-50,-50%);
    box-shadow: 0 0 10px 0 rgba(0,0,97,0.5);
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 20px 50px 50px 20px;
}




/* ''; */

.main-footer{
  color: black;
  background-color: rgba(0, 0, 0, 0.05);
  
  padding-bottom: 10px;
  position: relative;
  bottom: 0;
  width: 100%;
  text-align: left;
}

.footer{
  padding-left: 20px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.modal-header {
  background: black;
  color: white;
}

